@font-face {
  font-family: "Lato";
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.body {
  color: #444444;
  font-size: 14px;
  font-family: Arial, sans-serif;
  margin: 0;
  min-width: 320px;
}

.header {
  position: relative;
  font-family: Oswald, sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
  text-transform: uppercase;
  padding-top: 20px;
}

.header__nav {
  text-align: center;
}

.header__logo-link {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 20px;
  top: 12px;
  margin-right: 56px;
}

.header__logo-image {
  display: flex;
}

.header__logo-wrapper {
  display: flex;
  justify-content: center;
  padding-top: 4px;
  padding-bottom: 24px;
  border-bottom: 1px solid #E6E6E6;
}

.header__menu-button {
  position: absolute;
  top: 23px;
  right: 20px;
  width: 18px;
  height: 18px;
  border: none;
  padding: 0;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}

@media (min-width: 768px) {
  .header {
    display: flex;
  }

  .header__logo-wrapper {
    border-bottom: none;
  }

  .header__menu-button,
.header__logo-wrapper {
    display: none;
  }

  .header__logo-link {
    position: static;
  }
}
.site-list {
