.card {
  display: flex;
  flex-direction: column;
  border-bottom: none;
  padding: 20px 20px 25px;
}

.card__content-wrapper {
  display: flex;
  margin-bottom: 13px;
}

.card__image-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  flex-basis: 50%;
  min-height: 112px;
}

.card__text-content-wrapper {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  flex-basis: 50%;
}
.card__text-content-wrapper--show-more {
  align-items: center;
  text-align: center;
}

.card__icon {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30px;
  height: 30px;
  margin-bottom: 23px;
}
.card__icon::before, .card__icon::after {
  position: absolute;
  content: "";
  border: 1px solid #D3D3D3;
}
.card__icon::before {
  top: 0;
  width: 0;
  height: 30px;
}
.card__icon::after {
  top: 50%;
  width: 30px;
  height: 0;
}

