.contacts__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 28px 19px;
  margin: 0;

  &--with-background {
    background-color: $basic-grey;
  }
}

.contacts__welcome-text {
  font-family: Oswald, sans-serif;
  text-transform: uppercase;
  font-size: 16px;
  color: $text-black;
  line-height: 18px;
  margin: 0;
}

.contacts__address {
  font-size: 14px;
  line-height: 20px;
  margin: 0;
}

.contacts__map {
  width: 100%;
  height: 362px;
  background-image: url("../../images/rectangle.jpg");
  background-color: #aadaff;
  background-repeat: no-repeat;
  background-size: cover;
}

@media #{$desktop-width} {
  .contacts__wrapper {
      position: absolute;
      background-color: $color-white;
  }
}
